@media (min-aspect-ratio: 10/10){
	.homeContainer{
		width: 100%;
		display: flex;
		flex-direction: row;
		justify-content: space-around;
		box-sizing: border-box;
		padding: calc(45vh - 15vw) 2vh 0;
		opacity: 0;
		animation: 1s linear appearSlowly forwards;
		min-height: 300px;
		min-width: 600px;
	}
	.homeItem{
		width:30vw;
		height:30vw;
		color: white;
	}
	.homeLogo{
		width:25vw;
		height:25vw;
		background-color: transparent;
		color: #0d161c;
		border-radius: 20px;
		box-shadow: 0 0 0 .5vh white, 0 0 0 1.5vh #14232C, 0 0 0 2vh #005d41,0 0 1.5vw 1vw black;
		display: flex;
		flex-direction: column;
		justify-content: space-around;
		margin: 1vh 0 0;
	}
	.squareItem{
		width:40%;
		height:40%;
		display: inline-flex;
		flex-direction: column;
		justify-content: space-around;
		transition: 0.25s background-color, filter 0.25s, border-radius 0.25s, box-shadow 0.25s, transform 0.25s;
		box-sizing: border-box;
		border-radius: 20px;
		border: 2px solid white;
		background-color: #14232C;
		margin: 1vh 1vh 1vh;
		filter: brightness(0.6);
		box-shadow: 0 0 1.5vw black inset,0 0 0.5vw black;
		cursor: pointer;
	}
	.squareItem:hover{
		filter: brightness(1);
		border-radius: 50%;
		box-shadow: 0 0 1.5vw black inset, 0 0 0.5vw 0.25vw #005d41;
		transform: rotate(360deg);

	}
	.squareItem:active{
		background-color:#005d41;
	}
	.socialLink{
		height:15%;
		margin: auto;
	}
	.leftHandItems{
		height:12.5vw;
		width: 90%;
		background-color: #14232C;
		overflow: hidden;
		display: inline-flex;
		box-sizing: border-box;
		margin: 1vh 0 0;
		border-top-left-radius: 8vh;
		border-bottom-right-radius: 8vh;
		border-top-right-radius: 1vh;
		border-bottom-left-radius: 1vh;
		border: 2px solid white;
		filter: brightness(0.6);
		transition: filter 0.25s, box-shadow 0.20s,transform 0.25s;
		box-shadow: 0 0 0.5vw black;
		cursor: pointer;
	}
	.leftHandItems:hover{
		filter: brightness(1);
		transform: rotate(-5deg);
		box-shadow: 0 0 0.5vw black, 0 0 0.5vw 0.25vw #005d41;
	}
	.leftHandItems:active{
		box-shadow: 0 0 0 0.35vh #005d41, 0 0 0 0.6vh white;
		transform: rotate(5deg);
	}
	.leftHandSubDivLeft{
		width: 30%;
		height: 100%;
		border-top-left-radius: 20px;
		display: flex;
		flex-direction: column;
		justify-content: space-around;
		box-sizing: border-box;
		margin: 0 1vh 0;
	}
	.leftHandSubDivLeft img{
		width: 100%;
		display: inline-block;
	}
	.leftHandSubDivRight{
		width: 70%;
		height: 100%;
		border-bottom-right-radius: 20px;
	}
	.leftHandSubDivRightImg{
		width:100%;
		height: 100%;
		object-fit: fill;
	}
	.popUpTab{
		position: absolute;
		top: -100vh;
		left: 0;
		width: 100vw;
		height: 95vh;
		z-index: 4;
		display: flex;
		flex-direction: row;
		justify-content: space-around;
		transition: top 1s, bottom 2s;
		box-sizing: border-box;
		border-bottom: 3px solid #14232C;
		color: #14232C;
		visibility: hidden;
	}
	.popUpTabBody{
		width:100%;
		max-width: 2000px;
		height:100vh;
	}
	.popUpTabBodyCover{
		width:100%;
		height: 20vh;
		box-sizing: border-box;
		box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
		object-fit: cover;
		object-position: right top;
	}
	.popUpTabBodyCoverLogo{
		position: absolute;
		top:1vh;
		left:calc(50vw - 9vh);
		width:16vh;
		height: 16vh;
		padding: 1vh 1vh 1vh;
		background-color: #14232C;
		border-radius: 2vh;
		box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
	}
	.popUpTabTextContainer{
		width: 100%;
		height: 75vh;
		display: flex;
		flex-direction: row;
		justify-content: space-around;
		box-sizing: border-box;
		padding: 3vw 10vw;
		background: rgb(0,93,65);
		background: linear-gradient(180deg, rgba(0,93,65,0.8995973389355743) 0%, rgba(20,35,44,0.9023984593837535) 75%);
	}
	.popUpTabTextCards{
		box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.9), 0 6px 20px 0 rgba(0, 0, 0, 0.9);
		display: flex;
		flex-direction: column;
		justify-content: space-around;
		width: 70vh;
		box-sizing: border-box;
		padding: 1vh 1vh 1vh;
		font-size: 1vw;
		min-width: 30vw;
		max-height: 30vw;
		background: rgb(154,181,172);
		background: linear-gradient(180deg, rgba(154,181,172,1) 0%, rgba(255,255,255,1) 50%);
		border-radius: 1vw;
	}
	.popUpTabTextCardsImage{
		height: 30vw;
		max-width: 25vw;
		max-height: 25vw;
		object-fit: cover;
		object-position: right top;
	}
	.closeBtn{
		width: 5vh;
		height: 5vh;
		border-radius: 50%;
		background-color: #14232C;
		color: white;
		font-size: 3vh;
		text-align: center;
		display: flex;
		flex-direction: column;
		justify-content: space-around;
		cursor: pointer;
		position: absolute;
		top:2vh;
		right: 2vh;
		box-sizing: border-box;
		box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
		transition: background 0.25s, color 0.25s, border 0.25s, filter 0.25s;
	}
	.closeBtn:hover{
		background-color: white;
		color: #14232C;
	}
	.closeBtn:active{
		filter: brightness(0.9);
	}
	.servicetBtnCotnainer{
		position: absolute;
		bottom:5vh;
		left: 0;
		height: fit-content;
		box-shadow: 0 0 1vw black;
	}
	.servicetBtn{
		width: calc(100vw / 3);
		transition: 0.25s background-color, box-shadow 0.25s;
		box-sizing: border-box;
		border-top: 2px solid white;
		background-color: #005d41;
		padding: 1.5vh 1vh 1.5vh;
		font-size: 1.25vw;
		color: white;
		animation: 1s linear appearSlowly forwards;
		border-left: 1px solid white;
		border-right: 1px solid white;
		box-shadow: none;
	}
	.servicetBtn:hover{
		color: white;
		background-color: #014a34;
		box-shadow: 0 0 1vw 0.25vw #14232C inset;
	}
	.servicetBtn:active{
		box-shadow: 0 0 1.5vw 0.3vw #14232C inset;
	}
	.bigScreenOnly{
		display: block;
	}
	.smallScreenOnly{
		display: none;
	}
	.militaryBudge{
		width: 100%;
		height: fit-content;
		position: absolute;
		top: 8vh;
		left: 0;
		display: flex;
		flex-direction: row;
		justify-content: space-around;
		color: white;
		font-size: 2.5vh;
	}
	.emailInfo{
		color: white;
		position: absolute;
		width: 100%;
		text-align: center;
		font-size: 1vw;
		bottom: 14.25vh;
		left: 0;
		z-index: 3;
		cursor: default;
		display: flex;
		flex-direction: row;
		justify-content: space-around;
	}
	.emailInfoSubContainer{
		background-color:#14232C; 
		width:fit-content;
		padding:0.5vw 1vw 0.5vw;
		border-radius:1vh;
		box-shadow:0 0 0.75vw 0.25vw black,0 0 1.5vw #005d41,0 0 0.25vw #005d41 inset;
		cursor: pointer;
		transition: filter 0.25s, transform 0.25s;
		z-index: 1;
	}
	.emailInfoSubContainer:hover{
		filter: brightness(1.25);
	}
	.emailInfoSubContainer:active{
		transform: scale(0.9);
	}
	.emailPrompt{
		position: absolute;
		font-size: 2.5vh;
		font-weight: bold;
		top: 7vh;
		right: 1vw;
		color: white;
		z-index: 5;
		opacity: 0;
		transition: opacity 0.25s;
	}
}
