		animation: sqTOcir infinite 20s;
		animation-delay: 5s;
	}
	.sqTOcir3{
		animation: sqTOcir infinite 20s;
		animation-delay: 10s;
	}
	.sqTOcir4{
		animation: sqTOcir infinite 20s;
