}
}
@keyframes appearSlowly {
  0% {
    opacity: 0;
  }
  25% {
    opacity: 0;
  }
  75% {
    opacity: 0.6;
  }
  100% {
    opacity: 1;
  }
}
@keyframes swing {
	0%{
		transform: rotate(-5deg);
	}
	50%{
		transform: rotate(5deg);
	}
	100%{
		transform: rotate(-5deg);
	}
}

@keyframes sqTOcir {
	0%{
		border-radius: 20px;
	}
	12.5%{
		border-radius: 50%;
	}
	25%{
		border-radius: 20px;
	}
	100%{
		border-radius: 20px;
	}
}